body {
  margin: 0;
  font-family: sans-serif;
}

main {
  display: block;
  position: fixed;
  inset: 0;
}

#map {
  height: 100%;
}

#filter-form {
  z-index: 10;
  width: 400px;
  background-color: #fff;
  border: 1px solid gray;
  border-radius: 2px;
  position: absolute;
  top: 10px;
  left: 10px;
  overflow: hidden;
}

#filter-form-header {
  padding: 10px;
}

.expand-filters {
  float: right;
}

#filter-form-content {
  height: 0;
}

#filter-form-content > div {
  padding: 15px 35px 15px 15px;
}

#filter-form.expanded #filter-form-content {
  height: 100%;
}

#collapse-text {
  display: none;
}

#filter-form.expanded #collapse-text {
  display: block;
}

#filter-form.expanded #expand-text {
  display: none;
}

#filter-form input[type="text"], #filter-form input[type="date"], #filter-form input[type="datetime-local"], #filter-form input[type="time"] {
  height: 36px;
  width: 100%;
  max-width: 360px;
  background: #fff;
  border: 1px solid #c3c3c3;
  border-radius: 3px;
  margin-top: 5px;
  padding: 8px;
  line-height: 36px;
}

.mapboxgl-map {
  -webkit-tap-highlight-color: #0000;
  font: 12px / 20px Helvetica Neue, Arial, Helvetica, sans-serif;
  position: relative;
  overflow: hidden;
}

.mapboxgl-canvas {
  position: absolute;
  top: 0;
  left: 0;
}

.mapboxgl-map:-webkit-full-screen {
  width: 100%;
  height: 100%;
}

.mapboxgl-canary {
  background-color: salmon;
}

.mapboxgl-canvas-container.mapboxgl-interactive, .mapboxgl-ctrl-group button.mapboxgl-ctrl-compass {
  cursor: grab;
  -webkit-user-select: none;
  user-select: none;
}

.mapboxgl-canvas-container.mapboxgl-interactive.mapboxgl-track-pointer {
  cursor: pointer;
}

.mapboxgl-canvas-container.mapboxgl-interactive:active, .mapboxgl-ctrl-group button.mapboxgl-ctrl-compass:active {
  cursor: grabbing;
}

.mapboxgl-canvas-container.mapboxgl-touch-zoom-rotate, .mapboxgl-canvas-container.mapboxgl-touch-zoom-rotate .mapboxgl-canvas {
  touch-action: pan-x pan-y;
}

.mapboxgl-canvas-container.mapboxgl-touch-drag-pan, .mapboxgl-canvas-container.mapboxgl-touch-drag-pan .mapboxgl-canvas {
  touch-action: pinch-zoom;
}

.mapboxgl-canvas-container.mapboxgl-touch-zoom-rotate.mapboxgl-touch-drag-pan, .mapboxgl-canvas-container.mapboxgl-touch-zoom-rotate.mapboxgl-touch-drag-pan .mapboxgl-canvas {
  touch-action: none;
}

.mapboxgl-ctrl-bottom-left, .mapboxgl-ctrl-bottom-right, .mapboxgl-ctrl-top-left, .mapboxgl-ctrl-top-right {
  pointer-events: none;
  z-index: 2;
  position: absolute;
}

.mapboxgl-ctrl-top-left {
  top: 0;
  left: 0;
}

.mapboxgl-ctrl-top-right {
  top: 0;
  right: 0;
}

.mapboxgl-ctrl-bottom-left {
  bottom: 0;
  left: 0;
}

.mapboxgl-ctrl-bottom-right {
  bottom: 0;
  right: 0;
}

.mapboxgl-ctrl {
  clear: both;
  pointer-events: auto;
  transform: translate(0);
}

.mapboxgl-ctrl-top-left .mapboxgl-ctrl {
  float: left;
  margin: 10px 0 0 10px;
}

.mapboxgl-ctrl-top-right .mapboxgl-ctrl {
  float: right;
  margin: 10px 10px 0 0;
}

.mapboxgl-ctrl-bottom-left .mapboxgl-ctrl {
  float: left;
  margin: 0 0 10px 10px;
}

.mapboxgl-ctrl-bottom-right .mapboxgl-ctrl {
  float: right;
  margin: 0 10px 10px 0;
}

.mapboxgl-ctrl-group {
  background: #fff;
  border-radius: 4px;
}

.mapboxgl-ctrl-group:not(:empty) {
  box-shadow: 0 0 0 2px #0000001a;
}

@media (-ms-high-contrast: active) {
  .mapboxgl-ctrl-group:not(:empty) {
    box-shadow: 0 0 0 2px ButtonText;
  }
}

.mapboxgl-ctrl-group button {
  width: 29px;
  height: 29px;
  box-sizing: border-box;
  cursor: pointer;
  background-color: #0000;
  border: 0;
  outline: none;
  padding: 0;
  display: block;
  overflow: hidden;
}

.mapboxgl-ctrl-group button + button {
  border-top: 1px solid #ddd;
}

.mapboxgl-ctrl button .mapboxgl-ctrl-icon {
  width: 100%;
  height: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
  display: block;
}

@media (-ms-high-contrast: active) {
  .mapboxgl-ctrl-icon {
    background-color: #0000;
  }

  .mapboxgl-ctrl-group button + button {
    border-top: 1px solid ButtonText;
  }
}

.mapboxgl-ctrl-attrib-button:focus, .mapboxgl-ctrl-group button:focus {
  box-shadow: 0 0 2px 2px #0096ff;
}

.mapboxgl-ctrl button:disabled {
  cursor: not-allowed;
}

.mapboxgl-ctrl button:disabled .mapboxgl-ctrl-icon {
  opacity: .25;
}

.mapboxgl-ctrl-group button:first-child {
  border-radius: 4px 4px 0 0;
}

.mapboxgl-ctrl-group button:last-child {
  border-radius: 0 0 4px 4px;
}

.mapboxgl-ctrl-group button:only-child {
  border-radius: inherit;
}

.mapboxgl-ctrl button:not(:disabled):hover {
  background-color: #0000000d;
}

.mapboxgl-ctrl-group button:focus:focus-visible {
  box-shadow: 0 0 2px 2px #0096ff;
}

.mapboxgl-ctrl-group button:focus:not(:focus-visible) {
  box-shadow: none;
}

